.dot-two {
  animation: dot-two 2s infinite; }

.dot-three {
  animation: dot-three 2s infinite; }

@keyframes dot-one {
  0% {
    opacity: 0; }
  15% {
    opacity: 0; }
  25% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@keyframes dot-two {
  0% {
    opacity: 0; }
